import React, { useState } from 'react';
import {Grid} from '@mui/material';
import MultipleSelectChip from '../MultiSelect';
import CustomizedTables from '../Table';
import AlertDialog from '../Dialog';

function Home() {
  const [open, setOpen] = React.useState(null);

  const handleClose = () => {
    setOpen(null);
  };

  const onRowClick = (data: any = null) => {
    setOpen(data);
  }
    return (
      <Grid container className='w3-padding w3-margin-top'>
        <Grid item xs={12}>
          <MultipleSelectChip />
        </Grid>
        <Grid item xs={12}>
          <CustomizedTables onRowClick={onRowClick} />
        </Grid>
        <AlertDialog  handleClose={handleClose} open={Boolean(open)} />
      </Grid>
    );
  }

  export default Home;